import { mapActions } from "vuex";

export default {
  methods: {
    ...mapActions({
      getVerifyList: 'verification/getVerifyList',
      verifyOperate: 'verification/verifyOperate'
    }),
    // 按钮方法
    btnOperate(type, row) {
      // this.popupShow = true
      this.operateObj.action = type
      this.operateObj.userIds = row.id
      if (type == 'accept') {
        this.popupText = '通过'
      } else {
        this.popupText = '驳回'
      }
      this.$Modal.confirm({
        title: '操作',
        content: '<div style="color:#3B4453;text-align:center;font-size:18px">确定'+this.popupText+row.name+'身份审核？</div>',
        onOk: () => {
            this.operate()
        },
        onCancel: () => {
           this.popupShow = false
        }
      });
      
    },
    // 弹窗取消
    popupCancel () {
      this.popupShow = false
    },
    // 审核操作（弹窗确定）
    operate() {
      this.verifyOperate(this.operateObj)
        .then(res => {
          console.log(res)
          this.$Message.success('操作成功！')
          this.getList()
        })
        .catch(err => {
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
    },
    // 请求列表
    getList () {
      let params = {
        pageIndex: this.pageObj.pageIndex,
        pageSize: this.pageObj.pageSize,
        userType: this.userType,
        state: this.state
      }
      this.getVerifyList(params)
        .then(res => {
          console.log(res)
          this.pageObj.total = res.data.totalCount
          this.bodyList = res.data.dataList
        })
        .catch(err => {
          console.log(err)
          this.$Message.error({
            content: err.message,
            duration: 5
          })
        })
    }
  },
}