<template>
  <div class="company_verify">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr"></listSearch>
    <div class="content">
      
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="name">
            <span style="cursor:pointer" @click="goDetail(row)">{{ row.user_name }}</span>
          </template>
          <template slot-scope="{ row }" slot="idcardType">
            <span>{{ row.idcardType }}</span>
          </template>
          <template slot-scope="{ row }" slot="idcard">
            <span>{{ row.idcard }}</span>
          </template>
          <template slot-scope="{ row }" slot="sex">
            <span>{{ row.sex }}</span>
          </template>
          <template slot-scope="{ row }" slot="address">
            <span>{{ row.address }}</span>
          </template>
          <template slot-scope="{ row }" slot="phone">
            <span>{{ row.phone }}</span>
          </template>
          <template slot-scope="{ row }" slot="state">
            <span>{{ row.state }}</span>
          </template>
          <template slot-scope="{ row }" slot="action">
            <div>
              <Button type="primary" @click="btnOperate('accept', row)">同意</Button>
              <Button type="primary" @click="btnOperate('reject', row)">驳回</Button>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20" :total="pageObj.total" 
          show-elevator
          show-sizer 
          @on-page-size-change="pageSizeChange"
          @on-change="pageNumChange"/>
      </div>
    </div>
    <!-- 弹窗 -->
    <popupBox :show="popupShow" 
      @popupConfirm="operate" 
      @popupCancel="popupCancel"
      >
      <div class="popup_content">确定{{popupText}}?</div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import popupBox from '@/components/popup/popup-box'
import mixin from './mixin'
export default {
  name: 'company_verify',
  components: { listSearch, popupBox },
  mixins: [mixin],
  data () {
    return {
      userType: 2, // 1:个人 2:企业
      state: null, // 状态
      // 驳回操作参数
      operateObj: {
        userIds: null,
        action: null,
        reason: '1'
      },
      popupShow: false,
      popupText: '',
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      pageObj: {
        pageSize: 5,
        pageIndex: 0
      },
      searchArr: [
        {type: 'input', value: 'personalName', label: '个人姓名', placeholder: '请输入个人姓名'},
        {type: 'input', value: 'companyName', label: '企业名称', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '状态',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'},
      ],
      columns: [
        {
          title: '企业名称',
          key: 'name'
        },
        {
          title: '证件类型',
          key: 'enterpriseType'
        },
        {
          title: '证件号码',
