          key: 'idcard'
        },
        {
          title: '公司地址',
          key: 'address'
        },
        {
          title: '审核状态',
          key: 'stateDesc'
        },
        {
          title: '操作',
          width: '150',
          slot: 'action'
        }
      ],
      bodyList: []
    }
