      console.log(this.searchFormData)
    },
    // 翻页
    pageNumChange (val) {
      console.log(val)
      this.pageObj.pageIndex = val
    },
    // 修改pageSize
    pageSizeChange (val) {
      console.log(val)
      this.pageObj.pageSize = val
    },
    // 请求列表
    // getList () {
    //   let params = {
    //     pageIndex: this.pageObj.pageIndex,
    //     pageSize: this.pageObj.pageSize,
    //     userType: this.userType,
    //     state: this.state
    //   }
    //   this.commonApi.getVerifyList(params)
    //     .then(res => {
    //       console.log(res)
    //       this.pageObj.total = res.data.totalCount
    //       this.bodyList = res.data.dataList
    //     })
    //     .catch(err => {
    //       this.$Message.error({
    //         content: err.message,
    //         duration: 5
    //       })
    //     })
    // },
    // operate() {
    //   this.commonApi.verifyOperate(this.operateObj)
    //     .then(res => {
    //       console.log(res)
    //       this.$Message.success('操作成功！')
    //     })
    //     .catch(err => {
    //       this.$Message.error({
    //         content: err.message,
    //         duration: 5
    //       })
    //     })
    // },
    // 跳转详情页
    goDetail (data) {
      this.$router.push({
        path: 'verify_detail',
        params: data
      })
    }
  }
}
</script>
<style lang="less">
@import url('../../style/iview_table.less');
</style>
<style lang="less" scoped>
.company_verify {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.popup_content {
  text-align: center;
  font-size:18px;
  font-family:PingFangSC-Semibold,PingFang SC;
